export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {

	},
	methods: {
		formSubmit(e){
			var rule = [{
					name: "nickname",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "sd"
				},
			
			];
		
			var formData = e.detail.value;
			var checkRes = this.$validate.check(formData, rule);
			if (checkRes) {
				uni.showToast({
					title: "验证通过!",
					icon: "none"
				});
			} else {
				uni.showToast({
					title:  this.$validate.error,
					icon: "none"
				});
			}
		},
		
		
	}
}
